jects .e-company-list-project .e-company-list-project-name{font-size:1rem;font-weight:600;display:flex;justify-content:space-between}#resume-init .e-company-wrapper strong{font-size:14px}#resume-init .e-company-wrapper .company-heading{font-weight:800;background:#edeaea;margin-bottom:0.5rem;padding:0.5rem 0}#resume-init .details-section .p-details-field{display:flex;justify-content:space-between;max-width:392px}

